

.container {
  width: min(100% - 30px, 1080px);
  margin-inline: auto;
}

.flex {
  display: flex;
  justify-content: space-between;
}

h1, h2, p, span, a {
  color: #fff;
}

p {
  font-size: 1.8rem;
}

p + p {
  margin-top: 8px;
}

.user {
  margin-top: 60px;
}

header {
  height: 60px;
  background-color: rgb(255 255 255 / 5%);
}

h1 {
  font-size: 1.8rem;
  font-weight: 500;
  text-transform: uppercase;
  line-height: 60px;
  letter-spacing: 0.2px;
}

button {
  display: block;
  padding-bottom: 1px;
  margin-top: 12px;
  width: 90px;
  height: 36px;
  font-size: 1.6rem;
  font-weight: 600;
  border-radius: 50px;
  color: #fff;
  cursor: pointer;
}

.red {
  background-color: #e15945;
}

.green {
  background-color: #00a86b;
}

.movies {
  margin-top: 40px;

  p {
    color: #00f3b6;
  }
}

nav li {
  line-height: 60px;
  list-style: none;
  color: #fff;
  padding-right: 20px;
  cursor: pointer;
}

nav li a {
  text-decoration: none;
}

nav li a:hover {
  color: #00f3b6;
}